import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, ValorImposto } from '../../../../models/dtos/mfp/valorImposto';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referenciaInicial',
    order: OrderType.DESC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ValorImpostoSearchView: FC<SearchViewProps<ValorImposto>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'referenciaInicial',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Referência',
    },
    {
      dataField: 'vrSalarioMinimo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Vr.Sálario Mínimo',
    },
    {
      dataField: 'vrTetoINSS',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Vr.Teto INSS',
    },
    {
      dataField: 'vrDeducaoDependenteIRRF',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Vr.Dedução Dependente',
    },
    {
      dataField: 'vrDispensaRetencaoIRRF',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Vr.Dispensa Retenção',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Valor e imposto"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={{ ...getExtraData(props, 'ValorImpostoEditView', CLASS_NAME), hideRemoveButton: true }}
        addButtonProps={{ hideAddButton: true }}
      />
    </WindowComponent>
  );
};

export default ValorImpostoSearchView;
